  font-family: "Kumbh Sans";
  height: 100%;
}

body {
  height: 100%;
}

header {
  background-color: hsl(0, 0%, 100%);
  padding: 20px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 900px) {
  header {
    padding: 0 20px;
    height: 100px;
  }
}
@media (min-width: 1024px) {
  header {
    padding: 0 120px;
  }
}

.menu-section {
  display: flex;
  align-items: flex-end;
}
@media (min-width: 900px) {
  .menu-section {
    height: 100%;
    align-items: center;
  }
}

@media (min-width: 900px) {
  nav-menu {
    order: 2;
    height: 100%;
  }
}

.logo {
  margin: 0 10px 0 10px;
}
@media (min-width: 900px) {
  .logo {
    order: 1;
  }
}

.cart-section {
  display: flex;
  align-items: flex-end;
}
@media (min-width: 900px) {
  .cart-section {
    align-items: center;
  }
}

shopping-cart {
  margin: 0 20px 0 20px;
}
@media (min-width: 900px) {
  shopping-cart {
    margin: 0 40px 0 40px;
  }
}

.profile-btn {
  background-color: hsl(0, 0%, 100%);
  border: 0;
  padding: 0;
  border-radius: 25px;
}

.profile-img {
  height: 25px;
  width: 25px;
  border-radius: 25px;
  border: 1px solid hsl(0, 0%, 100%);
}
.profile-img:hover {
  cursor: pointer;
  border: 1px solid hsl(26, 100%, 55%);
}
@media (min-width: 900px) {
  .profile-img {
    height: 50px;
    width: 50px;
    border: 3px solid hsl(0, 0%, 100%);
  }
  .profile-img:hover {
    border: 3px solid hsl(26, 100%, 55%);
  }
